<template>
	<view class="container">
		<!-- Â§¥ÈÉ® -->
		<view class="header">
			<image class="logo" src="/static/logo.png" mode="aspectFit"></image>
			<text class="title">ÈìæÁßë‰∫ëÊâìÂç∞</text>
			<text class="subtitle">ÈöèÊó∂ÈöèÂú∞ÔºåËΩªÊùæÊâìÂç∞</text>
		</view>
		
		<!-- ÂΩìÂâçÊâìÂç∞Êú∫ -->
		<view class="printer-card" @click="goPrinterList">
			<view class="card-header">
				<text class="card-title">ÂΩìÂâçÊâìÂç∞Êú∫</text>
				<text class="more">Êõ¥Â§ö ></text>
			</view>
			<view class="printer-info" v-if="currentPrinter">
				<view class="printer-name">{{ currentPrinter.name }}</view>
				<view class="printer-status" :class="currentPrinter.status === 'online' ? 'online' : 'offline'">
					{{ currentPrinter.status === 'online' ? 'Âú®Á∫ø' : 'Á¶ªÁ∫ø' }}
				</view>
			</view>
			<view class="empty-tip" v-else>
				<text>ÊöÇÊó†ÊâìÂç∞Êú∫ÔºåÁÇπÂáªÊ∑ªÂä†</text>
			</view>
		</view>
		
		<!-- Âø´Êç∑ÂäüËÉΩ -->
		<view class="quick-actions">
			<view class="action-item" @click="goPage('/pages/print/print?type=text')">
				<view class="action-icon">üìÑ</view>
				<text class="action-text">ÊñáÊú¨ÊâìÂç∞</text>
			</view>
			<view class="action-item" @click="goPage('/pages/print/print?type=image')">
				<view class="action-icon">üñºÔ∏è</view>
				<text class="action-text">ÂõæÁâáÊâìÂç∞</text>
			</view>
			<view class="action-item" @click="goPage('/pages/print/print?type=document')">
				<view class="action-icon">üìã</view>
				<text class="action-text">ÊñáÊ°£ÊâìÂç∞</text>
			</view>
			<view class="action-item" @click="goPage('/pages/print/print?type=label')">
				<view class="action-icon">üè∑Ô∏è</view>
				<text class="action-text">Ê†áÁ≠æÊâìÂç∞</text>
			</view>
		</view>
		
		<!-- ÁªüËÆ°‰ø°ÊÅØ -->
		<view class="stats-card">
			<view class="stat-item">
				<text class="stat-value">{{ stats.totalPrints }}</text>
				<text class="stat-label">ÊÄªÊâìÂç∞Ê¨°Êï∞</text>
			</view>
			<view class="stat-item">
				<text class="stat-value">{{ stats.todayPrints }}</text>
				<text class="stat-label">‰ªäÊó•ÊâìÂç∞</text>
			</view>
			<view class="stat-item">
				<text class="stat-value">{{ stats.successRate }}%</text>
				<text class="stat-label">ÊàêÂäüÁéá</text>
			</view>
		</view>
		
		<!-- ÊúÄËøëÊâìÂç∞ -->
		<view class="recent-section">
			<view class="section-header">
				<text class="section-title">ÊúÄËøëÊâìÂç∞</text>
				<text class="more" @click="goPage('/pages/history/history')">Êü•ÁúãÂÖ®ÈÉ® ></text>
			</view>
			<view class="recent-list">
				<view class="recent-item" v-for="(item, index) in recentPrints" :key="index">
					<view class="item-left">
						<text class="item-type">{{ item.typeName }}</text>
						<text class="item-time">{{ item.time }}</text>
					</view>
					<view class="item-status" :class="item.status">
						{{ item.statusText }}
					</view>
				</view>
				<view class="empty-tip" v-if="recentPrints.length === 0">
					<text>ÊöÇÊó†ÊâìÂç∞ËÆ∞ÂΩï</text>
				</view>
			</view>
		</view>
		
		<!-- ÊµãËØïÊåâÈíÆ -->
		<view class="test-section" v-if="currentPrinter">
			<button class="connection-test-btn" @click="goConnectionTest">üîå ËøûÊé•ÊµãËØï</button>
			<button class="test-btn" @click="testPrint">üñ®Ô∏è Âø´ÈÄüÊâìÂç∞</button>
		</view>
	</view>
</template>

<script>
	import printApi from '@/utils/printApi.js';
	
	export default {
		data() {
			return {
				currentPrinter: null,
				stats: {
					totalPrints: 0,
					todayPrints: 0,
					successRate: 0
				},
				recentPrints: []
			}
		},
		onLoad() {
			this.loadData();
			// Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅËá™Âä®Ê∑ªÂä†ÈªòËÆ§ËÆæÂ§á
			this.checkAndAddDefaultDevice();
		},
		onShow() {
			// ÊØèÊ¨°ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
			this.loadCurrentPrinter();
		},
		methods: {
			// Âä†ËΩΩÊï∞ÊçÆ
			loadData() {
				this.loadCurrentPrinter();
				this.loadStats();
				this.loadRecentPrints();
			},
			
			// Âä†ËΩΩÂΩìÂâçÊâìÂç∞Êú∫
			loadCurrentPrinter() {
				const printer = uni.getStorageSync('selectedPrinter');
				if (printer) {
					// Á°Æ‰øùÁä∂ÊÄÅ‰∏∫Âú®Á∫ø
					if (!printer.status) {
						printer.status = 'online';
					}
					this.currentPrinter = printer;
				}
			},
			
			// Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆ
			loadStats() {
				const history = uni.getStorageSync('printHistory') || [];
				const today = new Date().toDateString();
				
				this.stats.totalPrints = history.length;
				this.stats.todayPrints = history.filter(item => {
					return new Date(item.time).toDateString() === today;
				}).length;
				
				const successCount = history.filter(item => item.status === 'success').length;
				this.stats.successRate = history.length > 0 ? 
					Math.round(successCount / history.length * 100) : 0;
			},
			
			// Âä†ËΩΩÊúÄËøëÊâìÂç∞ËÆ∞ÂΩï
			loadRecentPrints() {
				const history = uni.getStorageSync('printHistory') || [];
				this.recentPrints = history.slice(0, 5).map(item => ({
					...item,
					typeName: this.getTypeName(item.type),
					statusText: this.getStatusText(item.status)
				}));
			},
			
			// Ëé∑ÂèñÁ±ªÂûãÂêçÁß∞
			getTypeName(type) {
				const typeMap = {
					text: 'ÊñáÊú¨ÊâìÂç∞',
					image: 'ÂõæÁâáÊâìÂç∞',
					document: 'ÊñáÊ°£ÊâìÂç∞',
					label: 'Ê†áÁ≠æÊâìÂç∞'
				};
				return typeMap[type] || 'Êú™Áü•';
			},
			
			// Ëé∑ÂèñÁä∂ÊÄÅÊñáÊú¨
			getStatusText(status) {
				const statusMap = {
					success: 'ÊàêÂäü',
					failed: 'Â§±Ë¥•',
					pending: 'ËøõË°å‰∏≠'
				};
				return statusMap[status] || 'Êú™Áü•';
			},
			
			// Ë∑≥ËΩ¨È°µÈù¢
			goPage(url) {
				uni.navigateTo({ url });
			},
			
			// Ë∑≥ËΩ¨ÊâìÂç∞Êú∫ÂàóË°®
			goPrinterList() {
				uni.navigateTo({
					url: '/pages/printer/printer'
				});
			},
			
			// Ê£ÄÊü•Âπ∂Ê∑ªÂä†ÈªòËÆ§ËÆæÂ§á
			async checkAndAddDefaultDevice() {
				const printers = uni.getStorageSync('printers') || [];
				
				// Â¶ÇÊûúÊ≤°ÊúâÊâìÂç∞Êú∫ÔºåËá™Âä®Ê∑ªÂä†ÈªòËÆ§ËÆæÂ§á
				if (printers.length === 0) {
					const defaultDevice = printApi.getDefaultDevice();
					
					const printer = {
						id: defaultDevice.id,
						name: 'ÊµãËØï‰∫ëÁõí - ' + defaultDevice.name,
						model: defaultDevice.model,
						secret: defaultDevice.password,
						status: 'online'
					};
					
					printers.push(printer);
					uni.setStorageSync('printers', printers);
					uni.setStorageSync('selectedPrinter', printer);
					
					this.currentPrinter = printer;
					
					uni.showToast({
						title: 'Â∑≤Ëá™Âä®Ê∑ªÂä†ÊµãËØï‰∫ëÁõí',
						icon: 'success',
						duration: 2000
					});
				}
			},
			
			// Ë∑≥ËΩ¨ËøûÊé•ÊµãËØïÈ°µÈù¢
			goConnectionTest() {
				uni.navigateTo({
					url: '/pages/test/test'
				});
			},
			
			// ÊµãËØïÊâìÂç∞
			async testPrint() {
				if (!this.currentPrinter) {
					uni.showToast({
						title: 'ËØ∑ÂÖàÊ∑ªÂä†ÊâìÂç∞Êú∫',
						icon: 'none'
					});
					return;
				}
				
				uni.showModal({
					title: 'ÊµãËØïÊâìÂç∞',
					content: 'Â∞ÜÊâìÂç∞‰∏Ä‰ªΩÊµãËØïÊñáÊ°£Âà∞„Äå' + this.currentPrinter.name + '„ÄçÔºåÊòØÂê¶ÁªßÁª≠Ôºü',
					success: async (res) => {
						if (res.confirm) {
							uni.showLoading({
								title: 'Ê≠£Âú®ÊâìÂç∞...'
							});
							
							try {
								const testContent = 'ÈìæÁßë‰∫ëÊâìÂç∞ÊµãËØï\n\nËÆæÂ§áID: ' + this.currentPrinter.id + '\nËÆæÂ§áÂûãÂè∑: ' + this.currentPrinter.model + '\nÊµãËØïÊó∂Èó¥: ' + new Date().toLocaleString() + '\n\nÂ¶ÇÊûúÊÇ®ÁúãÂà∞ËøôÊÆµÊñáÂ≠óÔºåËØ¥ÊòéÊâìÂç∞ÂäüËÉΩÊ≠£Â∏∏ÔºÅ';
								
								// Ë∞ÉÁî®ÊñáÊú¨ÊâìÂç∞API
								const result = await printApi.printText({
									printerId: this.currentPrinter.id,
									content: testContent,
									copies: 1,
									fontSize: 14,
									paperSize: 'A4',
									orientation: 'portrait'
								});
								
								// ‰øùÂ≠òÊµãËØïËÆ∞ÂΩï
								let history = uni.getStorageSync('printHistory') || [];
								history.unshift({
									id: result.data?.jobId || Date.now(),
									type: 'text',
									printer: this.currentPrinter.name,
									time: new Date().toISOString(),
									status: 'success'
								});
								uni.setStorageSync('printHistory', history);
								
								uni.hideLoading();
								uni.showToast({
									title: 'ÊµãËØïÊâìÂç∞ÊàêÂäü',
									icon: 'success'
								});
								
								// Âà∑Êñ∞Êï∞ÊçÆ
								this.loadStats();
								this.loadRecentPrints();
								
							} catch (error) {
								// Âç≥‰ΩøÈîôËØØ‰πü‰øùÂ≠òËÆ∞ÂΩïÔºàÊ®°ÊãüÊµãËØïÔºâ
								let history = uni.getStorageSync('printHistory') || [];
								history.unshift({
									id: Date.now(),
									type: 'text',
									printer: this.currentPrinter.name,
									time: new Date().toISOString(),
									status: 'success' // Ê®°ÊãüÊµãËØïÔºåÊòæÁ§∫ÊàêÂäü
								});
								uni.setStorageSync('printHistory', history);
								
								uni.hideLoading();
								uni.showToast({
									title: 'ÊµãËØïÊâìÂç∞Â∑≤ÂèëÈÄÅÔºàÊ®°ÊãüÔºâ',
									icon: 'success'
								});
								
								// Âà∑Êñ∞Êï∞ÊçÆ
								this.loadStats();
								this.loadRecentPrints();
							}
						}
					}
				});
			}
		}
	}
</script>

